import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getLokasi } from "../../actions/lokasiAction";
import { getsupplierList } from "../../actions/supplierAction";
import { renderField, renderFieldSelect } from "../../renderField";

class FormLaporanMutasiBarangHead extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
    this.props.dispatch(getLokasi());
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <Field
              name="tanggalAwal"
              component={renderField}
              type="date"
              label="Tanggal Awal "
              placeholder="Masukan Tanggal Awal "
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="tanggalAkhir"
              component={renderField}
              type="date"
              label="Tanggal Akhir "
              placeholder="Masukan Tanggal Akhir "
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="lokasi"
              component={renderFieldSelect}
              options={this.props.lokasi.map((list) => {
                let data = {
                  value: list.kode_lokasi,
                  name: list.nama_lokasi,
                };
                return data;
              })}
              type="text"
              label="Supplier"
              placeholder="Masukan Supplier"
            />
          </div>
          <div className="col-md-12 col-lg-12 mt-3">
            <div className="row">
              <div className="col-lg-12 col-md-12">
                <div className="text-right">
                  <button className="btn btn-primary">
                    <i className="fa fa-search mr-1"></i>
                    Cari Data
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormLaporanMutasiBarangHead = reduxForm({
  form: "FormLaporanMutasiBarangHead",
  enableReinitialize: true,
})(FormLaporanMutasiBarangHead);
export default connect((state) => {
  return {
    supplier: state.provinsi.supplier,
    lokasi: state.provinsi.lokasi,
  };
})(FormLaporanMutasiBarangHead);
