import React, { Component } from "react";
import { getCetakUlang } from "../../actions/cetakUlangAction";
import { getUUID } from "../../actions/fakturAction";
import { getsupplierList } from "../../actions/supplierAction";
import Axios from "axios";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import notrespond from "../../Images/500.svg";
import Skeleton from "react-loading-skeleton";
import { Link } from "react-router-dom";
import { gagal } from "../../Alert";
import PrintNota from "../../Stocking/Component/PrintNota";
import HeaderCard from "../../HeaderCard";
import FormLaporanMutasiBarangHead from "./FormLaporanMutasiBarangHead";
import { AxiosMasterGet } from "../../Axios";
import { getToday } from "../../getDate";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class FormLaporanMutasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listNota: [],
      columns: [
        {
          dataField: "tgl_mutasi",
          text: "Tanggal",
        },
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "lokasi_asal",
          text: "Lokasi",
        },
        {
          dataField: "total_gross",
          text: "Gross",
        },
        {
          dataField: "total_pkg",
          text: "Pkg",
        },
        {
          dataField: "total_bruto",
          text: "Bruto",
        },
        {
          dataField: "total_netto",
          text: "Netto",
        },
        {
          dataField: "total_netto2",
          text: "Netto 2",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.print(row)}
                >
                  <i className="fa fa-print mr-1"></i>
                  Print
                </button>
              </div>
            );
          },
        },
      ],
      listPenerimaan: [],
    };
  }
  componentDidMount() {
    this.props.dispatch(getsupplierList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getCetakUlang());
  }
  getDetails(faktur) {
    Axios.get(
      serverNonMaster + "item/dc/mutasibarang/detail?noFaktur=" + faktur
    )
      .then((res) => this.setLocal(res.data))
      .catch(() =>
        gagal("Sepertinya ada kesalahan, Mohon Periksa Koneksi anda")
      );
  }
  setLocal(data) {
    localStorage.setItem("mutasiBarang", JSON.stringify(data.barangDetailList));
    this.print(data.tanggal, data.noBon);
  }
  print(data) {
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = data.detail_barang;
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        i++,
        ticket.kode_jenis,
        ticket.bruto,
        ticket.kadar_jual,
        ticket.netto,
        "<=====>",
        ticket.kode_jenis,
        ticket.kadar_jual2,
        ticket.netto2,
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    console.log(tableRows);
    var columTable = [
      "No",
      "Kode Jenis",
      "Bruto",
      "Harga (%)",
      "Netto",
      "<====>",
      "Kode Jenis 2",
      "Harga 2",
      "Netto 2",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    PrintNota(
      "Tanggal",
      data.tgl_mutasi,
      "",
      "",
      "Nomor Bon",
      data.no_bon,
      "",
      "",
      "ADMIN",
      getToday(),
      "",
      columTable,
      "MUTASI BARANG",
      tableRows
    );
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };

  caridata(data) {
    console.log("PENCARIAN....");
    AxiosMasterGet(
      `report/mutasi-stock/${data.lokasi}&${data.tanggalAwal}&${data.tanggalAkhir}`
    ).then((res) =>
      this.setState({
        listNota: res.data,
      })
    );
  }
  render() {
    return (
      <>
        <div className="container-fluid">
          <ol className="breadcrumb mb-2">
            <Link to="/cetakUlangBukti">
              <button type="button" className="btn btn-secondary">
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <HeaderCard title="Cetak Ulang Butki Mutasi Barang" />
            <div className="card-body">
              <br />
              <div className="row">
                <div className="col-lg-12">
                  <FormLaporanMutasiBarangHead
                    onSubmit={(data) => this.caridata(data)}
                  />
                </div>
                <div className="col-lg-12">
                  {this.state.listNota ? (
                    <ToolkitProvider
                      bootstrap4
                      keyField="no_mutasi"
                      data={this.state.listNota}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="col-lg-12 col-md-12 mt-5">
                          <div className="text-right">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Tuliskan Kode / Nama"
                              style={{ margin: 0 }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            headerClasses="header-table"
                            striped
                            hover
                            bordered={true}
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="NOT RESPOND"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width="100%" height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default connect()(FormLaporanMutasiBarang);
